import React, { useState, useEffect } from "react";
import Styles from "./Styles/WorksStyles.module.css";
import Aos from "aos";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGlobe, faCode } from "@fortawesome/free-solid-svg-icons";
import WebProjects from "./WebProjects";
import PythonProjects from "./PythonProjects";

function Works() {
  const [activeTab, setactiveTab] = useState(0);
  useEffect(() => {
    window.scrollTo(0, 0);
    Aos.init({
      duration: 1300,
    });
  }, []);

  return (
    <div className={Styles.worksPage}>
      <h1 className={`title ${Styles.title}`} data-aos="fade-up">
        My Works
      </h1>
      <div className={Styles.filter} data-aos="flip-up">
        <div className="tabs is-toggle">
          <ul>
            <li
              className={activeTab === 0 ? "is-active" : ""}
              onClick={() => {
                setactiveTab(0);
              }}
            >
              <a>
                <span className="icon is-small">
                  <FontAwesomeIcon icon={faGlobe} />
                </span>
                <span>Web</span>
              </a>
            </li>
            <li
              onClick={() => {
                setactiveTab(1);
              }}
              className={activeTab === 1 ? "is-active" : ""}
            >
              <a>
                <span className="icon is-small">
                  <FontAwesomeIcon icon={faCode} />
                </span>
                <span>Others</span>
              </a>
            </li>
          </ul>
        </div>
      </div>

      {activeTab === 0 ? (
        <WebProjects />
      ) : activeTab === 1 ? (
        <PythonProjects />
      ) : null}
    </div>
  );
}

export default Works;
