import React from "react";
import Styles from "./Styles/WorkStyles.module.css";
import { Carousel } from "react-responsive-carousel";
import LandingPage from "./Images/LandingPage.jpg";
import Learnish from "./Images/Learnish.jpg";
import LoginForm from "./Images/LoginForm.jpg";
import LazyLoad from "react-lazy-load";

function ProjectSlider() {
  return (
    <Carousel
      showArrows={false}
      showStatus={false}
      showIndicators={false}
      // showThumbs={false}
      infiniteLoop={true}
      autoPlay={true}
      stopOnHover={true}
      swipeable={true}
      dynamicHeight={true}
      interval={2000}
      className={Styles.Carousel}
      transitionTime={1000}
    >
      <div key="slide1" className={Styles.slide}>
        <LazyLoad>
          <img src={Learnish} alt="learnish" />
        </LazyLoad>
      </div>
      <div key="slide2" className={Styles.slide}>
        <LazyLoad>
          <img src={LoginForm} alt="login form" />
        </LazyLoad>
      </div>
      <div key="slide3" className={Styles.slide}>
        <LazyLoad>
          <img src={LandingPage} alt="landingPage" />
        </LazyLoad>
      </div>
    </Carousel>
  );
}

export default ProjectSlider;
