import React, { useEffect } from "react";
import Styles from "./Styles/WorksStyles.module.css";
import Aos from "aos";
import LazyLoad from "react-lazy-load";

function Project(props) {
  useEffect(() => {
    Aos.init({
      duration: 1300,
    });
  }, []);
  return (
    <div className={`card ${Styles.Project}`} data-aos="zoom-out">
      {props.tag === "web" ? (
        <div className="card-image">
          <figure className="image is-2by2">
            <LazyLoad>
              <img src={props.cover} alt="" />
            </LazyLoad>
          </figure>
        </div>
      ) : null}
      <div className="card-content">
        <p className={`title is-4 ${Styles.projectName}`}>{props.title}</p>
        <p className={`subtitle is-6 ${Styles.projectDesc}`}>
          {props.description}
        </p>
      </div>

      <footer className="card-footer">
        <p className="card-footer-item">
          <a href={props.github}>Code</a>
        </p>
        {props.tag === "web" ? (
          <p className="card-footer-item">
            <a href={props.preview}>Preview</a>
          </p>
        ) : null}
      </footer>
    </div>
  );
}

export default Project;
