import React, { useState } from "react";
import Styles from "./Styles/HeaderStyles.module.css";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

function Header() {
  const [navMenuState, setnavMenuState] = useState(false);
  const [burgerState, setburgerState] = useState(false);
  return (
    <nav
      className="navbar has-background-light is-fixed-top"
      role="navigation"
      aria-label="main navigation"
    >
      <div className="navbar-brand">
        <a className={`navbar-item ${Styles.brandWrap}`} href="/">
          <FontAwesomeIcon icon={faGithub} />
          <span className={Styles.brandName}>develSanx</span>
        </a>
        <span
          role="button"
          className={`navbar-burger ${burgerState ? "is-active" : ""}`}
          aria-label="menu"
          aria-expanded="false"
          onClick={() => {
            setnavMenuState((currentState) => !currentState);
            setburgerState((currentState) => !currentState);
          }}
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </span>
      </div>

      <div className={`navbar-menu ${navMenuState ? "is-active" : ""}`}>
        <div className="navbar-end">
          <HashLink to="/#home" className={`navbar-item ${Styles.navMenu}`}>
            Home
          </HashLink>
          <Link to="/about" className={`navbar-item ${Styles.navMenu}`}>
            About
          </Link>
          <Link to="/project" className={`navbar-item ${Styles.navMenu}`}>
            Works
          </Link>
          <HashLink to="/#contact" className={`navbar-item ${Styles.navMenu}`}>
            Contact
          </HashLink>
        </div>
      </div>
    </nav>
  );
}

export default Header;
